.contest__title {
  @include margin0-padding0;

  box-sizing: border-box;
  width: 280px;
  margin: 0 auto;
  padding: 50px 0 2px;

  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  text-align: center;
}

.contest__title span {
  display: none;
}

.contest__data-list {
  @include margin0-padding0;

  width: 280px;
  margin: 0 auto;
  padding-top: 38px;
  padding-bottom: 43px;

  border: none;
}

.contest__data-item {
  @include margin0-padding0;

  display: flex;
  flex-direction: column;

  padding-bottom: 20px;

  &:last-child {
    padding-bottom: 0;
  }
}

.contest__data-label {
  padding-left: 22px;

  font-size: 14px;
  line-height: 30px;
  font-weight: 400;

  color: $red-color;
  text-transform: uppercase;
}

.contest__data-input {
  @include margin0-padding0;

  padding: 10px 20px;
  font-family: $font-family;

  @include font18-30-400;

  border: 2px solid $grey-border;
}

.contest__data-input:hover,
.contest__data-input:focus {
  border: 2px solid $text-color;
}

.contest__data-input:focus {
  border: 2px solid $red-color;
}

.contest__achievements-list {
  @include margin0-padding0;
  border: none;

  background-color: $grey-bg;
}

.contest__achievements-item {
  @include margin0-padding0;
}

.contest__achievements-title {
  @include margin0-padding0;

  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $red-color;
  text-transform: uppercase;
}

.contest__achievements-label {
  @include margin0-padding0;
  @include font18-30-400;
}

.contest__apps-item {
  @include margin0-padding0;
}

.contest__apps-label {
  @include margin0-padding0;
  @include font18-30-400;
}

.contest__emo-area {
  @include margin0-padding0;

  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  border: none;
  width: 280px;
  margin: 0 auto;
  padding: 0;
  padding-bottom: 48px;
}

.contest__emo-area-title {
  @include margin0-padding0;

  padding-top: 38px;
  padding-left: 49px;
  padding-bottom: 15px;

  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  color: $red-color;


  text-transform: uppercase;
}

.contest__emo-area-item {

  display: block;
  max-width: 280px;
  box-sizing: border-box;

  padding-left: 20px;
  padding-top: 20px;
  font-family: $font-family;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  white-space: normal;

  border: 2px solid $grey-border;
}

.contest__button-wrapper {
  display: flex;
  flex-direction: column;
  width: 280px;
  margin: 0 auto;
}

.contest__submit {
  padding: 20px 0;
  margin-bottom: 40px;

  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  color: $white-color;
  text-transform: uppercase;
  text-align: center;

  background-color: $green-color;
  border: 2px solid $green-color;
}

.contest__submit:hover,
.contest__submit:focus {
  background-color: $green-color-dark;
  border: 2px solid $green-color-dark;
}

.contest__submit:active {
  color: $white-color-30;
  background-color: $green-color-dark;
  border: 2px solid $green-color-dark;
}

.contest__submit:disabled {
  background-color: $grey-border;
  border: 2px solid $grey-border;
}

.contest__legend {
  @include margin0-padding0;

  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
}

.contest__legend sup {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  color: $red-color;
}

@media (min-width: $tablet-width) {
  .contest__title {
    width: 620px;
    margin: 0 auto;

    padding: 95px 60px 30px;
    font-size: 18px;
  }

  .contest__title span {
    display: block;
  }
}

@media (min-width: $desktop-width) {
  .contest__title {
    width: 940px;
    margin: 0 auto;

    padding: 95px 90px 35px;
  }

  .contest__title span {
    display: inline-block;
  }
}
